import { Component, OnInit, OnDestroy } from '@angular/core';
import { TransferService } from '../shared/transfer.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit, OnDestroy {

  algo: string = "Bubble sort";
  private currentSize: number = 20;
  private sub: Subscription;
  isDone: boolean = true;
  constructor(
    private transferService: TransferService,
  ) {
    this.sub = this.transferService.getComplete().subscribe(this.completeObserver);
  }

  typeList: string[] = ["Bubble sort", "Selection sort", "Insertion sort", "Merge sort", "Quick sort"];
  spdList: string[] = ["Slow", "Average", "Fast"];

  ngOnInit() {
  }

  ngOnDestroy() {
    if (!this.sub) {
      this.sub.unsubscribe();
    }
  }

  selectAlgo(event) {
    this.algo = event.target.textContent;
  }

  changeSpeed(event) {
    let spd: string = event.target.textContent;
    let value: number;
    switch (spd) {
      case this.spdList[2]:
        value = 10;
        break;
      case this.spdList[1]:
        value = 500;
        break;
      default:
        value = 1000;
    }
    this.transferService.setSpd(value);
  }

  changeSize(sz: number) {
    this.currentSize = sz;
    this.transferService.setSize(sz);
  }

  sort() {
    this.transferService.setAlgo(this.algo);
  }

  randomize() {
    this.transferService.setRandom(this.currentSize);
  }

  turnOff(done: boolean) {
    this.isDone = done;
  }

  private completeObserver = {
    next: done => this.turnOff(done),
    error: err => console.error('Observer got an error: ' + err),
  };
}
