import { TransferService } from "./transfer.service";

export interface SortConstructor {
    new(ctx: CanvasRenderingContext2D, event: TransferService, arr: number[]): SortInterface;
}

export interface SortInterface {
    ctx: CanvasRenderingContext2D;
    mainList: number[];
    sort(): void;
    visualize(stop: boolean): void;
}


export function getSortClass(
    ctor: SortConstructor,
    ctx: CanvasRenderingContext2D,
    ev: TransferService,
    arr: number[]
): SortInterface {
    return new ctor(ctx, ev, arr);
}