
import { Color, fillText, spec } from "./util";
import { SortInterface } from "./sort-interface";
import { TransferService } from "./transfer.service";

export class MergeSort implements SortInterface {

    ctx: CanvasRenderingContext2D;
    mainList: number[];
    private extraList: number[];
    private extraList2: number[];
    size: number;
    private halfAbove = 250;

    // event
    private event: TransferService;

    // mergr sort
    private midList: number[] = [];
    private rangeList: number[][] = [];
    private swapList: number[][] = []; // i will be replace by number[i][0..1]

    constructor(ctx: CanvasRenderingContext2D, event: TransferService, arr: number[]) {
        this.event = event;
        this.ctx = ctx;
        this.mainList = arr;
        this.extraList = this.copy(arr);
        this.extraList2 = this.copy(arr);
        this.size = arr.length;

    }

    copy(arr: number[]): number[] {
        let temp: number[] = [];
        arr.forEach(val => temp.push(val));
        return temp;
    }

    sort(): void {
        // debugger
        this.rangeList = [];
        this.midList = [];
        this.swapList = [];
        for (let i = 0; i < this.size; i++) {
            this.swapList.push([]);
        }
        this.mergeSort(this.extraList, 0, this.size - 1);
        console.log(this.midList);
    }

    private tempCheck = false;
    mergeSort(arr: number[], lo: number, hi: number) {
        if (lo < hi) {
            let mid = (hi + lo) / 2;
            mid = Math.floor(mid);
            if (mid != 0 && !this.tempCheck) {
                this.midList.push(mid);
            } else if (mid == 0) {
                this.tempCheck = true;
            }

            this.mergeSort(arr, lo, mid);
            this.mergeSort(arr, mid + 1, hi);
            this.merge(arr, lo, mid, hi);
        }
    }

    merge(arr: number[], lo: number, mid: number, hi: number) {
        // debugger
        this.rangeList.push([]);
        this.rangeList[this.rangeList.length - 1].push(lo);
        this.rangeList[this.rangeList.length - 1].push(hi);
        const lLo: number = mid - lo + 1;
        const lHi: number = hi - mid;
        const loArr: number[] = arr.slice(lo, mid + 1); // lo -> mid
        const hiArr: number[] = arr.slice(mid + 1, hi + 1); // mid  + 1-> hi
        let u = 0, v = 0;
        for (let i = lo; i <= hi; i++) {
            if (u >= lLo) {
                this.swapList[i].push(v + mid + 1);
                arr[i] = hiArr[v++];
                continue;
            }
            if (v >= lHi) {
                this.swapList[i].push(u + lo);
                arr[i] = loArr[u++];
                continue;
            }
            if (loArr[u] <= hiArr[v]) {
                this.swapList[i].push(u + lo);
                arr[i] = loArr[u++];
            } else {
                this.swapList[i].push(v + mid + 1);
                arr[i] = hiArr[v++];
            }
        }
    }


    visualize(stop: boolean): void {
        this.divide(this, 0);
    }

    /**
     * 
     * @param obj : object contain canvas
     * @param x : id of each swap array from merge call (line 29)
     * @param y : dynamic bottom margin to move down
     * @param i : id of range list. i,e [lo, hi] in the merge call
     * @param blink : change color once again for highligh
     * @param down : true if columns were moved down.
     */
    moveDown(obj: MergeSort, x: number, y: number, i: number, blink: boolean, down: boolean) {
        // debugger
        let swapId = obj.swapList[x][0];
        if (!blink) {
            obj.ctx.fillRect(spec.lMargin + spec.distance * swapId, spec.bMargin - y, spec.wCol, -obj.mainList[swapId]);
            setTimeout(obj.moveDown, spec.spd, obj, x, y, i, true, down);
            return;
        }
        if (!down) {
            // obj.ctx.fillStyle = Color.origin;
            obj.ctx.clearRect(spec.lMargin + spec.distance * swapId, -y + 2, spec.wCol, -obj.mainList[swapId] + spec.bMargin - 2);
            obj.ctx.fillRect(spec.lMargin + spec.distance * x, spec.bMargin, spec.wCol, -obj.mainList[swapId]);
            fillText(obj.ctx, obj.mainList[swapId], spec.lMargin + spec.distance * x, 0, spec.wCol);
            setTimeout(obj.moveDown, spec.spd, obj, x, y, i, blink, true);
            return;
        }
        if (++x <= obj.rangeList[i][1]) {
            setTimeout(obj.moveDown, spec.spd, obj, x, y, i, false, false);
        } else {
            for (let u = obj.rangeList[i][0]; u <= obj.rangeList[i][1]; u++) {
                let temp = obj.swapList[u].shift();
                obj.mainList[u] = obj.extraList2[temp];
                // obj.extraList2[u] = obj.mainList[u];
            }
            for (let u = obj.rangeList[i][0]; u <= obj.rangeList[i][1]; u++) {
                obj.extraList2[u] = obj.mainList[u];
            }
            obj.drawAnimation(obj, i + 1, false, true);
        }
    }

    /**
     * 
     * @param obj : obj contain canvas
     * @param x : id column
     * @param y : dynamic bottom margin to move up
     * @param i : id of range list. i,e [lo, hi] in the merge call
     * @param blink : change color once again for highligh
     * @param up : true if columns were moved up.
     */
    moveUp(obj: MergeSort, x: number, y: number, i: number, pickRange: boolean, up: boolean) {
        // debugger
        // select-range
        if (!pickRange) {
            if (i - 1 >= 0) {
                obj.ctx.fillStyle = Color.blur;
                for (let u = obj.rangeList[i - 1][0]; u <= obj.rangeList[i - 1][1]; u++) {
                    obj.ctx.clearRect(spec.lMargin + spec.distance * u, spec.bMargin, spec.wCol, -obj.mainList[u]);
                    obj.ctx.fillRect(spec.lMargin + spec.distance * u, spec.bMargin, spec.wCol, -obj.mainList[u]);
                }
            }
            obj.ctx.fillStyle = Color.process;
            for (let u = obj.rangeList[i][0]; u <= obj.rangeList[i][1]; u++) {
                obj.ctx.clearRect(spec.lMargin + spec.distance * u, spec.bMargin, spec.wCol, -obj.mainList[u]);
                obj.ctx.fillRect(spec.lMargin + spec.distance * u, spec.bMargin, spec.wCol, -obj.mainList[u]);
            }
            setTimeout(obj.moveUp, spec.spd, obj, x, y, i, true, up);
            return;
        }
        if (!up) {
            for (let u = obj.rangeList[i][0]; u <= obj.rangeList[i][1]; u++) {
                obj.ctx.clearRect(spec.lMargin + spec.distance * u, 0, spec.wCol, -obj.mainList[u] + spec.bMargin);
                // obj.ctx.clearRect(spec.lMargin + spec.distance * u, spec.bMargin, spec.wCol, -obj.mainList[u]);
                obj.ctx.fillRect(spec.lMargin + spec.distance * u, spec.bMargin - y, spec.wCol, -obj.mainList[u]);
                fillText(obj.ctx, obj.mainList[u], spec.lMargin + spec.distance * u, -y, spec.wCol);
            }
            setTimeout(obj.moveUp, 5, obj, x, y, i, true, true);
            return;
        }
        obj.drawAnimation(obj, i, true, false);
    }


    divide(obj: MergeSort, id: number) {
        // debugger
        for (let j = obj.midList[id]; j < obj.size; j++) {
            obj.ctx.fillStyle = Color.blur;
            obj.ctx.clearRect(spec.lMargin + spec.distance * j, spec.bMargin, spec.wCol, -obj.mainList[j])
            obj.ctx.fillRect(spec.lMargin + spec.distance * j, spec.bMargin, spec.wCol, -obj.mainList[j])
        }
        let tOut = setTimeout(obj.divide, 500, obj, id + 1);
        if (id >= obj.midList.length) {
            clearTimeout(tOut);
            obj.drawAnimation(obj, 0, false, false);
        }
    }

    drawAnimation(obj: MergeSort, i: number, moveDone: boolean, swap: boolean) {
        let l = obj.rangeList.length;
        if (!moveDone && i < l) {
            obj.moveUp(obj, obj.rangeList[i][0], obj.halfAbove, i, false, false);
            return;
        }
        if (!swap && i < l) {
            obj.moveDown(obj, obj.rangeList[i][0], obj.halfAbove, i, false, false);
            return;
        }
        // set complete color
        obj.ctx.fillStyle = Color.complete;
        for (let u = 0; u < obj.mainList.length; u++) {
            let x = spec.lMargin + spec.distance * u;
            let y = spec.bMargin;
            let w = spec.wCol;
            obj.ctx.clearRect(x, y, w, -obj.mainList[u]);
            obj.ctx.fillRect(x, y, w, -obj.mainList[u]);
        }
        obj.event.setComplete(true);
    }
}