import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Subscription } from 'rxjs';
import { SelectionSort } from '../shared/selection-sort';
import { getSortClass, SortInterface } from '../shared/sort-interface';
import { TransferService } from '../shared/transfer.service';
import { Color, fillText, randomArray, spec } from '../shared/util';
import { MergeSort } from './../shared/merge-sort';

@Component({
  selector: 'app-selector',
  templateUrl: './selector.component.html',
  styleUrls: ['./selector.component.scss']
})
export class SelectorComponent implements OnInit, OnDestroy {

  @ViewChild('canvas', { static: true })
  canvas: ElementRef<HTMLCanvasElement>;

  private ctx: CanvasRenderingContext2D;

  private mainList: number[] = [];
  private sortVisualizer: SortInterface;
  private subscriptions: Subscription[] = [];

  constructor(
    private reciever: TransferService,
  ) {
    // sub the random
    let sub = this.reciever.getRandom().subscribe(this.randomObserver);
    this.subscriptions.push(sub);

    // sub the random
    sub = this.reciever.getSize().subscribe(this.sizeChangeObserver);
    this.subscriptions.push(sub);

    // sub the spd change
    sub = this.reciever.getSpd().subscribe(this.speedChangeObserver)
    this.subscriptions.push(sub);

    // sub the algo change
    sub = this.reciever.getAlgo().subscribe(this.algoObserver)
    this.subscriptions.push(sub);
  }

  ngOnInit() {
    // Inital canvas
    this.ctx = this.canvas.nativeElement.getContext('2d');
    spec.wCanvas = this.canvas.nativeElement.width;
    spec.hCanvas = this.canvas.nativeElement.height;
    this.ctx.fillStyle = Color.origin;
    this.ctx.textAlign = 'left';
    this.ctx.strokeStyle = 'red';
    this.ctx.font = "14px Consolas";
    this.ctx.translate(0, spec.hCanvas);

    //inital array
    this.randomize(20);
  }

  sort(algo: string) {
    this.reciever.setComplete(false);
    switch (algo) {
      case 'Selection sort':
        this.sortVisualizer = getSortClass(SelectionSort, this.ctx, this.reciever, this.mainList);
        break;
      case 'Merge sort':
        this.sortVisualizer = getSortClass(MergeSort, this.ctx, this.reciever, this.mainList);
        break;
      default:
        return;
    }
    this.sortVisualizer.sort();
    this.sortVisualizer.visualize(true);
  }

  changeSpeed(spd: number) {
    spec.spd = spd;
  }

  changeSize(sz: number) {
    let maxDistance = (spec.wCanvas - 2 * spec.lMargin) / sz;
    maxDistance = Math.floor(maxDistance);
    spec.distance = maxDistance;
    spec.wCol = Math.floor(maxDistance / 2);
    spec.size = sz;
    // console.table({ size: sz, dis: spec.distance, maxDis: maxDistance, width: spec.wCol });
    this.randomize(sz);
  }

  randomize(sz: number) {
    this.ctx.fillStyle = Color.origin;
    this.ctx.clearRect(0, 0, spec.wCanvas, -spec.hCanvas);
    this.mainList = randomArray(sz);
    for (let i = 0; i < sz; i++) {
      let x = spec.lMargin + spec.distance * i;
      let y = spec.bMargin;
      let w = spec.wCol;
      this.ctx.fillRect(x, y, w, -this.mainList[i]);
      fillText(this.ctx, this.mainList[i], x, 0, w);
    }
  }

  ngOnDestroy() {
    // unsubscribe to ensure no memory leaks
    this.subscriptions.forEach(sub => {
      sub.unsubscribe();
    });
  }

  /** 
   * observer const
  */
  private randomObserver = {
    next: x => this.randomize(x),
    error: err => console.error('Observer got an error: ' + err),
  };

  private sizeChangeObserver = {
    next: x => this.changeSize(x),
    error: err => console.error('Observer got an error: ' + err),
  };
  private speedChangeObserver = {
    next: x => this.changeSpeed(x),
    error: err => console.error('Observer got an error: ' + err),
  };

  private algoObserver = {
    next: s => this.sort(s),
    error: err => console.error('Observer got an error: ' + err),
  };
}
