import { changeColor, Color, fillText, spec } from "./util";
import { SortInterface } from "./sort-interface";
import { TransferService } from "./transfer.service";

export class SelectionSort implements SortInterface {

    ctx: CanvasRenderingContext2D;
    mainList: number[];
    private minList: number[][];
    private extraList: number[];
    size: number;
    private event: TransferService;

    constructor(ctx: CanvasRenderingContext2D, event: TransferService, arr: number[]) {
        this.event = event;
        this.ctx = ctx;
        this.mainList = arr;
        this.extraList = this.copy(arr);
        this.size = arr.length;
    }

    copy(arr: number[]): number[] {
        let temp: number[] = [];
        arr.forEach(val => temp.push(val));
        return temp;
    }

    sort(): void {
        // debugger
        this.minList = [];
        for (let i = 0; i < this.size - 1; i++) {
            let min = i;
            this.minList.push([]);
            for (let j = i + 1; j < this.size; j++) {
                if (this.extraList[min] > this.extraList[j]) {
                    min = j;
                    this.minList[i].push(min);
                }
            }
            let temp = this.extraList[min];
            this.extraList[min] = this.extraList[i];
            this.extraList[i] = temp;
        }
    }



    visualize(stop: boolean): void {
        this.drawAnimation(this, 0, 0, -1, false);
    }


    swapAnimation(obj: SelectionSort, i: number, j: number, pre: number, blink: boolean, swap: boolean) {
        // debugger
        if (j < obj.size) {
            let min = pre;
            if (!blink) {
                // obj.ctx.fillStyle = Color.swapping;
                if (swap) {
                    changeColor(obj, j, min, Color.swapping);
                    changeColor(obj, min, j, Color.swapping);
                } else {
                    changeColor(obj, j, j, Color.minimum);
                    changeColor(obj, min, min, Color.minimum);
                }
                setTimeout(obj.swapAnimation, spec.spd, obj, i, j, pre, true, swap);
                return;
            }
            if (!swap) {
                // visualize swap
                obj.ctx.fillStyle = Color.preSwap;
                obj.ctx.clearRect(spec.lMargin + spec.distance * j, 0, spec.wCol, -obj.mainList[j] + spec.bMargin);
                obj.ctx.clearRect(spec.lMargin + spec.distance * min, 0, spec.wCol, -obj.mainList[min] + spec.bMargin);
                obj.ctx.fillRect(spec.lMargin + spec.distance * j, spec.bMargin, spec.wCol, -obj.mainList[min]);
                obj.ctx.fillRect(spec.lMargin + spec.distance * min, spec.bMargin, spec.wCol, -obj.mainList[j]);
                fillText(obj.ctx, obj.mainList[min], spec.lMargin + spec.distance * j, 0, spec.wCol);
                fillText(obj.ctx, obj.mainList[j], spec.lMargin + spec.distance * min, 0, spec.wCol);
                setTimeout(obj.swapAnimation, spec.spd, obj, i, j, pre, false, true);
                return;
            }
            // swap
            let temp = obj.mainList[min];
            obj.mainList[min] = obj.mainList[j];
            obj.mainList[j] = temp;
            //draw new canvas with 0..j is sorted.
            obj.ctx.clearRect(0, 0, spec.wCanvas, -spec.hCanvas);
            obj.ctx.fillStyle = Color.origin;
            for (let u = 0; u < obj.size; u++) {
                obj.ctx.fillRect(spec.lMargin + spec.distance * u, spec.bMargin, spec.wCol, -obj.mainList[u]);
                fillText(obj.ctx, obj.mainList[u], spec.lMargin + spec.distance * u, 0, spec.wCol);
            }
            for (let x = 0; x <= j; x++) {
                changeColor(obj, x, x, Color.complete);
            }
            i = ++j;
            const id = setTimeout(obj.drawAnimation, spec.spd, obj, i, j, pre, false);
            if (j == obj.size - 1) {
                clearTimeout(id);
                changeColor(obj, j, j, Color.complete);
                obj.event.setComplete(true);
            }
        }
    }

    drawAnimation(obj: SelectionSort, i: number, j: number, pre: number, last: boolean) {
        // i inner for loop
        // j outer for loop
        // check through the array
        // debugger
        if (i == obj.minList[j][0]) {
            changeColor(obj, pre, pre, Color.origin);
            changeColor(obj, i - 1, i - 1, Color.origin);
            pre = obj.minList[j].shift();
            changeColor(obj, i, i, Color.minimum);
        } else if (i == j) {
            changeColor(obj, i, i, Color.minimum);
            pre = j;
        } else {
            if (i - 1 != pre)
                changeColor(obj, i - 1, i - 1, Color.origin);
            changeColor(obj, i, i, last ? Color.origin : Color.process);
        }
        i++;
        if (i == obj.size) {
            if (i - 1 == pre) last = true;
            if (!last) {
                // show color of last value
                setTimeout(obj.drawAnimation, spec.spd, obj, i - 1, j, pre, true);
            } else {
                // start to swap
                obj.swapAnimation(obj, i, j, pre, false, false);
            }
            return;
        }
        setTimeout(obj.drawAnimation, spec.spd, obj, i, j, pre, false);
    }

}
