import { Injectable } from '@angular/core';
import { Observable, of, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class TransferService {
  private $size = new Subject<number>();
  private $speed = new Subject<number>();
  private $algo = new Subject<string>();
  private $random = new Subject<number>();
  private $completed = new Subject<boolean>();

  constructor() {
  }

  setRandom(sz: number) {
    this.$random.next(sz);
  }

  getRandom(): Observable<number> {
    return this.$random.asObservable();
  }

  setComplete(done: boolean) {
    this.$completed.next(done);
  }

  getComplete(): Observable<boolean> {
    return this.$completed.asObservable();
  }

  setAlgo(algo: string) {
    this.$algo.next(algo);
  }

  getAlgo(): Observable<string> {
    return this.$algo.asObservable();
  }

  setSpd(spd: number) {
    this.$speed.next(spd);
  }

  getSpd(): Observable<number> {
    return this.$speed.asObservable();
  }

  setSize(sz: number) {
    this.$size.next(sz);
  }

  getSize(): Observable<number> {
    return this.$size.asObservable();
  }

}
