import { SelectionSort } from "./selection-sort";

export function randomIntFromInterval(min, max) { // min and max included 
    return Math.floor(Math.random() * (max - min + 1) + min);
}

export function randomArray(size: number): number[] {
    let tempArray: number[] = [];
    for (let i = 0; i < size; i++) {
        tempArray.push(randomIntFromInterval(30, 220));
    }
    return tempArray;
}

// global color
export const Color = {
    origin: '#4895ef',
    blur: '#83c5be',
    process: '#e76f51',
    complete: '#f9c74f',
    minimum: '#e76f51',
    preSwap: '#2a9d8f',
    swapping: '#e76f51',
    text: '#E76F51'
}


export const spec = {
    hCanvas: 550,
    wCanvas: 1280,
    distance: 61,
    wCol: 30,
    lMargin: 25,
    bMargin: -25,
    spd: 500,
    size: 20
}

// change color at pos x and value i
export function changeColor(obj: SelectionSort, pos: number, i: number, color: string): void {
    // debugger
    obj.ctx.fillStyle = color;
    obj.ctx.clearRect(spec.lMargin + spec.distance * pos, spec.bMargin, spec.wCol, -obj.mainList[i]);
    obj.ctx.fillRect(spec.lMargin + spec.distance * pos, spec.bMargin, spec.wCol, -obj.mainList[i]);
}


export function fillText(obj: CanvasRenderingContext2D, val: number, x: number, y: number, w: number) {
    let preColor = obj.fillStyle;
    obj.fillStyle = Color.text;
    obj.fillText(val.toString(), x, y, w);
    obj.fillStyle = preColor;
}